import { useState } from 'react';

export default function Home() {
  const [todos, setTodos] = useState([
    { id: 1, title: 'Buy groceries', status: 'PENDING' },
    { id: 2, title: 'Complete project', status: 'IN_PROGRESS' },
    { id: 3, title: 'Workout', status: 'COMPLETED' }
  ]);

  const [title, setTitle] = useState('');

  const handleAdd = () => {
    if (title.trim() === '') return;

    const newTodo = {
      id: todos.length + 1,
      title,
      status: 'PENDING'
    };
    setTodos([...todos, newTodo]);
    setTitle('');
  };

  return (
    <div className="min-h-screen bg-gray-100 p-6">
      <div className="max-w-xl mx-auto bg-white p-4 rounded shadow">
        <h1 className="text-2xl font-bold mb-4">Todo List</h1>

        <div className="flex space-x-2 mb-4">
          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            placeholder="Enter task title"
            className="flex-1 border border-gray-300 p-2 rounded"
          />
          <button
            onClick={handleAdd}
            className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
          >
            Add
          </button>
        </div>

        <ul className="space-y-2">
          {todos.map((todo) => (
            <li
              key={todo.id}
              className="flex justify-between items-center bg-gray-50 p-2 rounded border"
            >
              <span>{todo.title}</span>
              <span className="text-sm text-gray-500">{todo.status}</span>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
